import './style.css';
import React, { Component } from 'react';
import Aliment from '../aliment/Aliment';

class AlimentList extends Component {

  constructor(props) {
    super(props);
    this.state = {
      aliments: this.props.aliments,
    };
    this.buildList = this.state.aliments.map(aliment =>
      <Aliment key={aliment.id} aliment={aliment} />
    );
  }
  
  render() {
    return (
      <div>
        <h1>List aliments</h1>
        <table className="table table-striped">
          <thead>
            <tr>
              <th scope="col">Label</th>
              <th scope="col">kcal/100g</th>
              <th scope="col">kcal/1</th>
            </tr>
          </thead>
          <tbody>
            {this.buildList}
          </tbody>
        </table>
      </div>
    );
  }
}

export default AlimentList;



