import './App.css';
import React, { Component } from 'react';
import AlimentEditor from './components/aliment-editor/AlimentEditor';
import AlimentModel from './models/AlimentModel';
import AlimentList from './components/AlimentList/AlimentList';
import AlimentStorageProcessor from './AliementStorageProcessor';

class App extends Component {

  constructor(props) {
    super(props);
    this.alimentStorageProcessor = new AlimentStorageProcessor();
    this.state = {
      aliment: new AlimentModel('Poire'),
      aliments: this.findAllAliments(),
    }
  }

  /**
   * adding a new aliment to the list
   * @param {Aliment} aliment 
   */
  addAlimentToList = aliment => {
    const aliments = this.alimentStorageProcessor.addToList(aliment);
    this.setState({ aliments: aliments });
  }

  /**
   * retrieving all the aliments form the list
   */
  findAllAliments() {
    return this.alimentStorageProcessor.findAll();
  }

  render() {
    return (
      <div className="container-fluid">
        <h1>App Title</h1>
        <br />
        {/* ajouter un aliment */}
        <AlimentEditor aliment={this.state.aliment} onSubmit={this.addAlimentToList} />
        <br /><br />
        {/* lister les aliments*/}
        <AlimentList aliments={this.state.aliments} />
      </div>
    );
  }
}

export default App;



