import * as common from './common';

class IdGenerator {

    generateAlimentId() {
        return this.generateId(common.KEY_ALIMENTS_ID_GENERATOR);
    }

    generateId(key) {
        this.initGenerator(key);
        let newId = parseInt(localStorage.getItem(key)) + 1;
        localStorage.setItem(key, newId);
        return newId;
    }

    initGenerator(key) {
        if (!localStorage.getItem(key)) {
            localStorage.setItem(key, 0);
        }
    }
}

export default IdGenerator;


