import './style.css';
import React, { Component } from 'react';

class Aliment extends Component {

  constructor(props) {
    super(props);
    this.state = {
      aliment: this.props.aliment,
    };
  }

  render() {
    return (
      <tr key={this.state.aliment.id}>
        <td>{this.state.aliment.label}</td>
        <td>{this.state.aliment.energyPer100g}</td>
        <td>{this.state.aliment.energyPerUnit}</td>
      </tr>
    );
  }
}

export default Aliment;



